import React, { useEffect } from "react";
import Header from "../commonComponent/header";
import { Link, useLocation, useNavigate } from "react-router-dom";
import SideBar from "../commonComponent/sideBar";

function Transaction() {
  const location = useLocation();
  const navigate = useNavigate();

  const queryParams = new URLSearchParams(location.search);
  const activeTab = queryParams.get("tab") || 1;

  useEffect(() => {
    if (activeTab == 2) {
      const allTabs = document.querySelectorAll(".tab-pane");
      const allTabLinks = document.querySelectorAll(".nav-item");

      allTabs.forEach((tab) => tab.classList.remove("show", "active"));
      allTabLinks.forEach((link) => link.classList.remove("active"));

      document.getElementById("users-tab").classList.add("active");
      document.getElementById("users").classList.add("show", "active");
    }
  }, [activeTab]);

  const handleTab = (tabId) => {
    queryParams.set("tab", tabId);
    queryParams.delete("page");
    navigate({ search: queryParams.toString() }, { replace: true });
  };
  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <div className="d-flex justify-content-between">
            <div
              className="tabs nav nav-tabs common_tabs"
              id="reportsTab"
              role="tablist"
            >
              <label
                className="tab nav-item active"
                htmlFor="radio-1"
                id="sales-tab"
                data-bs-toggle="tab"
                href="#sales"
                role="tab"
                aria-controls="sales"
                aria-selected="true"
                onClick={() => handleTab(1)}
              >
                User's Transaction
              </label>
              <label
                className="tab nav-item"
                htmlFor="radio-2"
                id="users-tab"
                data-bs-toggle="tab"
                href="#users"
                role="tab"
                aria-controls="users"
                aria-selected="false"
                onClick={() => handleTab(2)}
              >
                Merchant's Transaction
              </label>
            </div>
          </div>
          <div className="tab-content mt-4" id="reportsTabContent">
            <div
              className="tab-pane common_table fade show active"
              id="sales"
              role="tabpanel"
              aria-labelledby="sales-tab"
            >
              <div className="row comman_design mx-0">
                <div className="col-12">
                  <div className="row align-items-center justify-content-between py-md-4 py-3 px-md-3 px-2">
                    <div className="col-md-auto mb-md-0 mb-3">
                      <div className="headleft">
                        <h2>User Transaction Management</h2>
                      </div>
                    </div>
                    <div className="col-md-4 d-flex align-items-center justify-content-end">
                      <div className="me-3">
                        <button className="btn bg-main text-white rounded-pill px-3 py-2 border-0">
                          Export
                        </button>
                      </div>
                      <div className="searchh_box position-relative">
                        <input
                          className="form-control"
                          type="search"
                          placeholder="Search"
                        />
                        <button>
                          <i className="fas fa-search" />
                        </button>
                      </div>
                      <div className="dropdown filterdropdown">
                        <button
                          className="btn btn-secondary"
                          type="button"
                          data-bs-toggle="dropdown"
                          aria-expanded="false"
                        >
                          <i className="fas fa-filter" />
                        </button>
                        <div className="dropdown-menu">
                          <form className="form-design row mx-0" action>
                            <div className="col-12 form-group">
                              <label htmlFor>Year</label>
                              <select
                                className="form-select"
                                aria-label="Default select example"
                              >
                                <option selected>2001</option>
                                <option value={1}>2002</option>
                                <option value={2}>2003</option>
                                <option value={2}>2004</option>
                                <option value={2}>2005</option>
                                <option value={2}>2006</option>
                                <option value={2}>2007</option>
                                <option value={2}>2008</option>
                                <option value={2}>2009</option>
                                <option value={2}>2010</option>
                              </select>
                            </div>
                            <div className="col-12 form-group">
                              <label htmlFor>Months</label>
                              <select
                                className="form-select"
                                aria-label="Default select example"
                              >
                                <option selected>Jan</option>
                                <option value={1}>Feb</option>
                                <option value={2}>Mar</option>
                                <option value={2}>Apr</option>
                                <option value={2}>May</option>
                                <option value={2}>Jun</option>
                                <option value={2}>July</option>
                                <option value={2}>August</option>
                                <option value={2}>Sep</option>
                                <option value={2}>Oct</option>
                                <option value={2}>Nov</option>
                                <option value={2}>Dec</option>
                              </select>
                            </div>
                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-12">
                  <div className="row comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
                    <div className="col-12">
                      <div className="table-responsive">
                        <table className="table table-hover">
                          <thead>
                            <tr>
                              <th>S.No.</th>
                              <th>User Name</th>
                              <th>Date</th>
                              <th>Amount</th>
                              <th>Payment Method</th>
                              <th>Plan Purchased</th>
                              <th>Action</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr>
                              <td>1</td>
                              <td>Jennifer Garcia</td>
                              <td>10/10/2024</td>
                              <td>100 ED</td>
                              <td>Apple Pay</td>
                              <td>Basic</td>
                              <td>
                                <div className="d-flex justify-content-center">
                                  <Link
                                    to="/admin/view-transaction"
                                    className="Table_btn me-2"
                                  >
                                    <i className="fa fa-eye" />
                                  </Link>
                                  <a href="javascript:;" className="Table_btn">
                                    <i className="fa fa-trash" />
                                  </a>
                                </div>
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div
              className="tab-pane common_table fade"
              id="users"
              role="tabpanel"
              aria-labelledby="users-tab"
            >
              <div className="row comman_design mx-0">
                <div className="col-12">
                  <div className="row align-items-center justify-content-between py-md-4 py-3 px-md-3 px-2">
                    <div className="col-md-auto mb-md-0 mb-3">
                      <div className="headleft">
                        <h2> Merchant Transaction Management</h2>
                      </div>
                    </div>
                    <div className="col-md-4 d-flex align-items-center justify-content-end">
                      <div className="me-3">
                        <button className="btn bg-main text-white rounded-pill px-3 py-2 border-0">
                          Export
                        </button>
                      </div>
                      <div className="searchh_box position-relative">
                        <input
                          className="form-control"
                          type="search"
                          placeholder="Search"
                        />
                        <button>
                          <i className="fas fa-search" />
                        </button>
                      </div>
                      <div className="dropdown filterdropdown">
                        <button
                          className="btn btn-secondary"
                          type="button"
                          data-bs-toggle="dropdown"
                          aria-expanded="false"
                        >
                          <i className="fas fa-filter" />
                        </button>
                        <div className="dropdown-menu">
                          <form className="form-design row mx-0" action>
                            <div className="col-12 form-group">
                              <label htmlFor>Year</label>
                              <select
                                className="form-select"
                                aria-label="Default select example"
                              >
                                <option selected>2001</option>
                                <option value={1}>2002</option>
                                <option value={2}>2003</option>
                                <option value={2}>2004</option>
                                <option value={2}>2005</option>
                                <option value={2}>2006</option>
                                <option value={2}>2007</option>
                                <option value={2}>2008</option>
                                <option value={2}>2009</option>
                                <option value={2}>2010</option>
                              </select>
                            </div>
                            <div className="col-12 form-group">
                              <label htmlFor>Months</label>
                              <select
                                className="form-select"
                                aria-label="Default select example"
                              >
                                <option selected>Jan</option>
                                <option value={1}>Feb</option>
                                <option value={2}>Mar</option>
                                <option value={2}>Apr</option>
                                <option value={2}>May</option>
                                <option value={2}>Jun</option>
                                <option value={2}>July</option>
                                <option value={2}>August</option>
                                <option value={2}>Sep</option>
                                <option value={2}>Oct</option>
                                <option value={2}>Nov</option>
                                <option value={2}>Dec</option>
                              </select>
                            </div>
                          </form>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
                <div className="col-12">
                  <div className="row comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
                    <div className="col-12">
                      <div className="table-responsive">
                        <table className="table table-hover">
                          <thead>
                            <tr>
                              <th>S.No.</th>
                              <th>Merchant Name</th>
                              <th>Date</th>
                              <th>Amount</th>
                              <th>Payment Method</th>
                              <th>Plan Purchased</th>
                              <th>Action</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr>
                              <td>1</td>
                              <td> Bella Tom</td>
                              <td>10/10/2024</td>
                              <td>500 ED</td>
                              <td>Google Pay</td>
                              <td>Basic</td>
                              <td>
                                <div className="d-flex justify-content-center">
                                  <Link
                                    to="/admin/view-transaction"
                                    className="Table_btn me-2"
                                  >
                                    <i className="fa fa-eye" />
                                  </Link>
                                  <a href="javascript:;" className="Table_btn">
                                    <i className="fa fa-trash" />
                                  </a>
                                </div>
                              </td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Transaction;
